@import './../../styles/sass/vars.scss';
@import './../../styles/sass/mixins.scss';

.inner {
    height: 423px;
    max-width: 100%;
    background-image: url(./../../assets/img/map.png);
    background-size: cover;
    background-position: 58%, 50%;
    background-repeat: no-repeat;
    margin-bottom: 52px;
    @media screen and (min-width: 576px) {
        height: 509px;
    }
}

.mapInfo {
    display: none;
    @media screen and (min-width: 1280px) {
        display: block;
    }
}

.photographerInfo {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 10px;
    padding-bottom: 20px;

} 
.ava {
    width: 67px;
    height: 67px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin-right: 10px;
}

.name {
    color: $textColor;
    @include text(16px, 500, 20px);
}
.work {
    color: $textColor;
    @include text(14px, 400, 21px);
    padding-top: 5px;
    padding-bottom: 7px;
}
.moreInfo {
    color: $linkColor;
    @include text(14px, 500, 21px);
    width: 156px;
}

.phInfo {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.slider {
    border: 1px solid $secondaryColor;
    width: 100%;
    max-width: 400px;
    height: 380px;
    margin: 0 auto;
    @media screen and (min-width: 1280px) {
        display: none;

    }

}

.mapInfo {
    width: 420px;
    height: 470px;
    padding: 20px 0;
}

.container {
    @extend %container;
}